<!-- 侧边栏 -->
<template lang="html">
    <div class="sidebar clear">
      <div class="sidebar-left">
        <ul>
          <router-link :to="{path:'/mycar'}" tag="li" active-class="act">我的车队</router-link>
          <router-link :to="{path:'/inquiry'}" tag="li" active-class="act">证件查询</router-link>
          <router-link :to="{path:'/shared'}" tag="li" active-class="act">共享管理</router-link>
        </ul>
      </div>
      <router-view class="sidebar-right"></router-view>
    </div>


</template>

<script>
export default {
}
</script>

<style lang="less">
.sidebar {
  width: 1200px;
  margin: 0 auto;
  background: #FAFBFF;
  border: none;
  .sidebar-left {
    float: left;
    width: 200px;
    height: 760px;
    ul {
      height: 100%;
      border: 1px solid #e8e6f3;
      li {
        height: 50px;
        border-bottom: 1px solid #e8e6f3;
        text-align: center;
        line-height: 50px;
        color: #717171;
        font-size: 16px;
      }
      .act {
        color: #2e53ac;
        background: #f5f6fb;
        font-weight: 700;
      }
    }
  }
  .sidebar-right {
    width: 940px;
    height: 760px;
    float: right;
    background: white;
    margin-right: 30px;
  }
}
</style>



